@keyframes second-rotation {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes minute-rotation {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes cricle-light {
  from {
    background-color: black;
  }

  50% {
    background-color: black;
  }

  to {
    background-color: red;
  }
}
